@font-face {
    font-family: WAmeIcons;
    src: url(/wp-content/plugins/creame-whatsapp-me/public/css/data:font/woff;base64,) format('woff');
    font-display: swap;
}

.whatsappme {
    position: fixed;
    z-index: 1000;
    right: 20px;
    bottom: 20px;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
    font-size: 16px;
    line-height: 1.625em;
    color: #262626;
    transform: scale3d(0, 0, 0);
    transition: transform .3s ease-in-out;
    user-select: none;
    -ms-user-select: none;
    -moz-user-select: none;
    -webkit-user-select: none;
}

.call-me{
    position: fixed; bottom: 15px; left: 20px;
    z-index: 10000;
}
.call-me img{
    width: 0px;
    height: 0px;
    transition: all ease-in-out 0.3s;
}
.call-me.active img{
    width: 60px;
    height: 60px;
}

.whatsappme *,
.whatsappme:after,
.whatsappme:before {
    box-sizing: border-box
}

.whatsappme svg,
.whatsappme svg path {
    fill: currentColor!important
}

.whatsappme.whatsappme--show {
    transform: scale3d(1, 1, 1);
    transition: transform .5s cubic-bezier(.18, .89, .32, 1.28)
}

.whatsappme__button {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    position: absolute;
    z-index: 2;
    bottom: 8px;
    right: 8px;
    height: 60px;
    min-width: 60px;
    max-width: 95vw;
    background-color: #25d366;
    color: #fff;
    border-radius: 30px;
    box-shadow: 1px 6px 24px 0 rgba(7, 94, 84, .24);
    cursor: pointer;
    transition: background-color .2s linear;
    -webkit-tap-highlight-color: transparent
}

.whatsappme__button:hover {
    background-color: #128c7e;
    transition: background-color 1.5s linear
}

.whatsappme--chatbox .whatsappme__button {
    background-color: #128c7e;
    transition: background-color .2s linear
}

.whatsappme--chatbox .whatsappme__button:hover {
    background-color: #075e54
}

.whatsappme__button:active {
    background-color: #075e54;
    transition: none
}

@supports (-webkit-overflow-scrolling:touch) {
    .whatsappme--chatbox .whatsappme__button {
        background-color: #34b7f1
    }
    .whatsappme--chatbox .whatsappme__button:hover,
    .whatsappme__button:active {
        background-color: #228bb9
    }
}

.whatsappme__button svg {
    width: 36px;
    height: 36px;
    margin: 12px 12px
}

.whatsappme__button .whatsappme__button__send {
    margin: 12px 11px 12px 13px
}

.whatsappme__button__image {
    position: absolute;
    top: 1px;
    right: 1px;
    width: 58px;
    height: 58px;
    border-radius: 50%;
    overflow: hidden;
    opacity: 0
}

.whatsappme__button__image img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover
}

.whatsappme--show .whatsappme__button__image {
    animation: wame_image_loop 20s linear 5s infinite normal both
}

.whatsappme--image .whatsappme__button__image {
    opacity: 1;
    animation: none
}

.whatsappme__button__sendtext {
    padding: 0;
    max-width: 0;
    font-weight: 600;
    line-height: 60px;
    white-space: nowrap;
    opacity: 0;
    overflow: hidden;
    transition: none
}

.whatsappme--chatbox .whatsappme__button__sendtext {
    padding: 0 4px 0 24px;
    max-width: 200px;
    opacity: 1;
    transition: max-width .2s linear, opacity .4s ease-out .2s
}

.whatsappme__badge {
    position: absolute;
    top: -4px;
    right: -4px;
    width: 20px;
    height: 20px;
    border: none;
    border-radius: 50%;
    background: #e82c0c;
    font-size: 12px;
    font-weight: 600;
    line-height: 20px;
    text-align: center;
    box-shadow: none;
    opacity: 0;
    pointer-events: none
}

.whatsappme__badge.whatsappme__badge--in {
    animation: wame_badge_in .5s cubic-bezier(.27, .9, .41, 1.28) 1 both
}

.whatsappme__badge.whatsappme__badge--out {
    animation: wame_badge_out .4s cubic-bezier(.215, .61, .355, 1) 1 both
}

.whatsappme--chatbox .whatsappme__button {
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .3)
}

.whatsappme .whatsappme__button__send path {
    fill: none!important;
    stroke: #fff!important
}

.whatsappme .whatsappme__button__send .wame_svg__plain {
    stroke-dasharray: 1097;
    stroke-dashoffset: 1097;
    animation: wame_plain 6s .2s ease-in-out infinite
}

.whatsappme .whatsappme__button__send .wame_svg__chat {
    stroke-dasharray: 1020;
    stroke-dashoffset: 1020;
    animation: wame_chat 6s 3.2s ease-in-out infinite
}

.whatsappme__tooltip {
    position: absolute;
    top: 14px;
    right: 76px;
    max-width: calc(100vw - 105px);
    height: 32px;
    padding: 0 14px;
    border: none;
    border-radius: 16px;
    background: #fff;
    color: #075e54;
    line-height: 31px;
    white-space: nowrap;
    box-shadow: 0 1px 4px 0 rgba(0, 0, 0, .4);
    opacity: 0
}

.whatsappme__tooltip::after {
    font: normal normal 400 20px/1em WAmeIcons;
    content: '\e900';
    position: absolute;
    top: 6px;
    right: -12px;
    color: #fff;
    text-align: right;
    text-shadow: 2px 1px 3px rgba(0, 0, 0, .4)
}

.whatsappme__tooltip div {
    overflow: hidden;
    text-overflow: ellipsis
}

.whatsappme--tooltip .whatsappme__tooltip {
    animation: wame_tootlip 20s linear 5s 1 normal both
}

.whatsappme--chatbox .whatsappme__button__send,
.whatsappme__button__open {
    display: block
}

.whatsappme--chatbox .whatsappme__button__image,
.whatsappme--chatbox .whatsappme__button__open,
.whatsappme--chatbox .whatsappme__tooltip,
.whatsappme__button__send {
    display: none
}

.whatsappme__box {
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1;
    width: calc(100vw - 40px);
    max-width: 400px;
    min-height: 270px;
    border-radius: 32px;
    background: #ede4dd url(/wp-content/plugins/creame-whatsapp-me/public/images/background.webp) center repeat-y;
    background-size: 100% auto;
    box-shadow: 0 2px 6px 0 rgba(0, 0, 0, .5);
    overflow: hidden;
    transform: scale3d(0, 0, 0);
    opacity: 0;
    transition: opacity .4s ease-out, transform 0s linear .3s
}

.whatsappme--webp .whatsappme__box {
    background-image: url(/wp-content/plugins/creame-whatsapp-me/public/images/background.webp)
}

.whatsappme--chatbox .whatsappme__box {
    opacity: 1;
    transform: scale3d(1, 1, 1);
    transition: opacity .2s ease-out, transform 0s linear
}

.whatsappme__header {
    float: none;
    display: block;
    position: static;
    width: 100%;
    height: 70px;
    padding: 0 26px;
    margin: 0;
    background-color: #2e8c7d;
    color: rgba(255, 255, 255, .5)
}

.whatsappme__header svg {
    width: 120px;
    height: 100%
}

.whatsappme__close {
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    position: absolute;
    top: 18px;
    right: 24px;
    width: 34px;
    height: 34px;
    border-radius: 50%;
    background: #000;
    color: #fff;
    text-align: center;
    opacity: .4;
    cursor: pointer;
    transition: opacity .3s ease-out;
    -webkit-tap-highlight-color: transparent
}

.whatsappme__close:hover {
    opacity: .6
}

.whatsappme__close svg {
    display: block;
    width: 12px;
    height: 12px;
    margin: auto
}

.whatsappme__box__scroll {
    max-height: calc(100vh - 100px);
    padding: 20px 0 80px 0;
    overflow: hidden auto
}

.whatsappme__box__scroll::-webkit-scrollbar {
    width: 5px;
    background: rgba(0, 0, 0, 0)
}

.whatsappme__box__scroll::-webkit-scrollbar-thumb {
    border-radius: 3px;
    background: rgba(0, 0, 0, 0)
}

.whatsappme__box__scroll:hover::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, .2)
}

.whatsappme__message {
    position: relative;
    min-height: 80px;
    padding: 20px;
    margin: 0 26px 26px;
    border-radius: 32px;
    background-color: #fff;
    color: #4a4a4a;
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, .3)
}

.whatsappme__message:before {
    content: '';
    display: block;
    position: absolute;
    bottom: 30px;
    left: -18px;
    width: 18px;
    height: 18px;
    background-image: url(data:image/png;base64,);
    background-size: 100%
}

.whatsappme__message a {
    text-decoration: underline;
    color: #2e8c7d
}

.whatsappme__copy {
    position: absolute;
    bottom: 9px;
    left: 28px;
    color: #2e8c7d;
    font-size: 11px;
    letter-spacing: .2px;
    line-height: 16px;
    opacity: .4;
    transition: opacity .25s
}

.whatsappme__copy:hover {
    opacity: .8;
    transition: opacity .5s ease-out .5s
}

.whatsappme__copy a,
.whatsappme__copy a:active,
.whatsappme__copy a:hover {
    color: inherit;
    text-decoration: none
}

.whatsappme__copy svg {
    width: 40px;
    height: 10px;
    vertical-align: inherit
}

.whatsappme--left {
    right: auto;
    left: 20px
}

.whatsappme--left .whatsappme__button {
    right: auto;
    left: 8px;
    -ms-flex-direction: row-reverse;
    -webkit-flex-direction: row-reverse;
    flex-direction: row-reverse
}

.whatsappme--left.whatsappme--chatbox .whatsappme__button__sendtext {
    padding: 0 24px 0 4px
}

.whatsappme--left .whatsappme__tooltip {
    right: auto;
    left: 76px
}

.whatsappme--left .whatsappme__tooltip::after {
    content: '\e901';
    left: -12px;
    right: auto;
    text-align: left;
    text-shadow: -2px 1px 3px rgba(0, 0, 0, .4)
}

.whatsappme--left .whatsappme__box {
    right: auto;
    left: 0
}

.whatsappme--left .whatsappme__copy {
    left: auto;
    right: 28px;
    text-align: right
}

.whatsappme--dark .whatsappme__header {
    background: #075e54
}

.whatsappme--dark .whatsappme__box {
    background: #000
}

.whatsappme--dark .whatsappme__message {
    background: #505050;
    color: #d8d8d8
}

.whatsappme--dark .whatsappme__message:before {
    background-image: url(data:image/png;base64,)
}

.whatsappme--dark .whatsappme__copy {
    color: #25d366
}

@media (prefers-color-scheme:dark) {
    .whatsappme--dark-auto .whatsappme__header {
        background: #075e54
    }
    .whatsappme--dark-auto .whatsappme__box {
        background: #000
    }
    .whatsappme--dark-auto .whatsappme__message {
        background: #505050;
        color: #d8d8d8
    }
    .whatsappme--dark-auto .whatsappme__message:before {
        background-image: url(data:image/png;base64,)
    }
    .whatsappme--dark-auto .whatsappme__copy {
        color: #25d366
    }
}

[dir=rtl] .whatsappme__close {
    right: auto;
    left: 24px
}

[dir=rtl] .whatsappme__message:before {
    left: auto;
    right: -18px;
    transform: rotateY(180deg)
}

[dir=rtl] .whatsappme__button {
    -ms-flex-direction: row-reverse;
    -webkit-flex-direction: row-reverse;
    flex-direction: row-reverse
}

[dir=rtl] .whatsappme--left .whatsappme__button {
    -ms-flex-direction: row;
    -webkit-flex-direction: row;
    flex-direction: row
}

@media (max-width:480px),
(orientation:landscape) and (max-width:767px) {
    .whatsappme {
        bottom: 6px;
        right: 6px
    }
    .whatsappme--left {
        right: auto;
        left: 6px
    }
    .whatsappme__box {
        width: calc(100vw - 12px);
        min-height: 0
    }
    .whatsappme__header {
        height: 55px
    }
    .whatsappme__close {
        top: 13px;
        width: 28px;
        height: 28px
    }
    .whatsappme__box__scroll {
        max-height: calc(100vh - 67px);
        padding: 15px 0 70px 0
    }
    .whatsappme__message {
        padding: 16px;
        margin: 0 20px 20px;
        line-height: 24px
    }
}

@media (max-width:360px) {
    .whatsappme__copy {
        width: 70px
    }
}

@keyframes wame_badge_in {
    from {
        opacity: 0;
        transform: translateY(50px)
    }
    to {
        opacity: 1;
        transform: translateY(0)
    }
}

@keyframes wame_badge_out {
    from {
        opacity: 1;
        transform: translateY(0)
    }
    to {
        opacity: 0;
        transform: translateY(-20px)
    }
}

@keyframes wame_plain {
    0%,
    100%,
    50% {
        stroke-dashoffset: 1097
    }
    45%,
    5% {
        stroke-dashoffset: 0
    }
}

@keyframes wame_chat {
    0%,
    100%,
    50% {
        stroke-dashoffset: 1020
    }
    45%,
    5% {
        stroke-dashoffset: 0
    }
}

@keyframes wame_image_loop {
    0% {
        opacity: 0
    }
    20%,
    3% {
        opacity: 1
    }
    100%,
    23% {
        opacity: 0
    }
}

@keyframes wame_tootlip {
    0% {
        opacity: 0;
        transform: scaleY(0)
    }
    1%,
    20% {
        opacity: 1;
        transform: scaleY(1)
    }
    100%,
    23% {
        opacity: 0;
        transform: scaleY(1)
    }
}