	1.12 - Unit Test Issue Fix
	1.13 - Default Color
	1.14 - IE9 and above override
	1.15 - Responsive*/


/*--------------------------------------------------------------
	1.0 - Reset
	--------------------------------------------------------------*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
and,
address,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video,
input,
textarea,
select
{
    background: transparent;
    border: 0;
    font-size: 100%;
    margin: 0;
    outline: 0;
    padding: 0;
    vertical-align: baseline;
    
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section
{
    display: block;
}
body
{
    line-height: 1;
}
abbr[title],
dfn[title]
{
    border-bottom: 1px dotted;
    cursor: help;
}
del
{
    text-decoration: line-through;
}
hr
{
    background: transparent;
    border: 0;
    clear: both;
    color: transparent;
    height: 1px;
    margin: 0;
    padding: 0;
}
mark
{
    background-color: #ffffb3;
    font-style: italic;
}
input,
select
{
    vertical-align: middle;
}
ins
{
    background-color: red;
    color: white;
    text-decoration: none;
}
ol,
ul
{
    list-style: none;
}
table
{
    border-collapse: collapse;
    border-spacing: 0;
}



/*--------------------------------------------------------------
	1.1 - Body
	--------------------------------------------------------------*/

body
{
    font-size: 20px;
    font-weight: 600;
    line-height: 32px;
}
*,
*:before,
*:after
{
            box-sizing: border-box; 
       -moz-box-sizing: border-box;
    -webkit-box-sizing: border-box;
}
body,
html
{
    overflow-x: hidden;
    width: 100%;
}
html
{
    height: 100%;
}

.uppercase,
.uppercase *
{
    text-transform: uppercase;
}


/*--------------------------------------------------------------
	1.2 - Transition
	--------------------------------------------------------------*/
a,
input[type='submit'],
input[type='reset'],
.radio-label input[type='radio'] + span:before,
input,
input:focus,
textarea,
textarea:focus
{
            transition: all .3s linear; 
       -moz-transition: all .3s linear;
        -ms-transition: all .3s linear;
         -o-transition: all .3s linear;
    -webkit-transition: all .3s linear;
}


/*--------------------------------------------------------------
	1.3 - Headings
	--------------------------------------------------------------*/

h1
{
    font-size: 44px;
}
h2
{
    font-size: 40px;
}
h3
{
    font-size: 34px;
}
h4
{
    font-size: 30px;
}
h5
{
    font-size: 24px;
}
h6
{
    font-size: 20px;
}

h1,
h2,
h3,
h4,
h5,
h6
{
    font-weight: 700;
    line-height: normal;
    margin-bottom: 20px;
}

h1:empty,
h2:empty,
h3:empty,
h4:empty,
h5:empty,
h6:empty,
p:empty
{
    display: none;
}

