}

.selection-box
{
    clear: both; 
    position: relative;
}
.selection-box:before,
.selection-box:after
{
    content: '';
    pointer-events: none;
    position: absolute;
}
.selection-box:before
{
    border-left: 7px solid transparent;
    border-right: 7px solid transparent;
    border-top: 7px solid; 
    bottom: 0;
    height: 0;
    margin: auto;
    right: 17px;
    top: 5px;
    z-index: 1;
}
.selection-box:after
{
    border-left: 0px solid rgba(0, 0, 0, .15);
    border-right: 0px solid rgba(0, 0, 0, .15); 
    height: calc(100% - 2px);
    right: 0;
    top: 1px;
    width: 48px;
}
.selection-box select
{
    margin-top: 0;
}


