	--------------------------------------------------------------*/
.wrapper
{
    clear: both;
    float: left; 
    margin: 0;
    padding: 0;
    width: 100%;
}
.inner-wrapper
{
    float: left;
    margin: 0;
    overflow: hidden; 
    padding: 0;
    width: 100%;
}

.container
{
    clear: both; 
    margin: 0 auto;
    position: relative;
