.loader
{
    height: 100%;
    left: 0;
    position: fixed;
    text-align: center; 
    top: 0;
    width: 100%;
    z-index: 10000000;
}
.loader .loader-inner
{
    left: 0;
    margin: 0 auto; 
    position: absolute;
    right: 0;
    top: 50%;
            transform: translateY(-50%);
       -moz-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    width: 100%;
}
.loader .loader-inner h1
{
    font-size: 5em;
    line-height: 70px;
    margin: 35px 0 0; 
    text-transform: uppercase;
}
.loader .loader-inner h1:after
{
            animation: Dots 2s cubic-bezier(0, .39, 1, .68) infinite; 
    -webkit-animation: Dots 2s cubic-bezier(0, .39, 1, .68) infinite;
    content: '';
    position: absolute;
}

#owl
{
    animation: scale1 2s infinite;
    background: #fbb600;
    height: 208px;
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    transition: all .2s ease-out; 
    width: 208px;
}

@keyframes scale1
{
    0%
    {
        border-radius: 10%; 
        box-shadow: 0 25px 25px rgba(0, 0, 0, .1);
        transform: scale(1);
    }
    25%
    {
        border-radius: 20%; 
        box-shadow: 0 5px 5px rgba(0, 0, 0, .15);
        transform: scale(.5);
    }
    50%
    {
        border-radius: 30%; 
        box-shadow: 0 15px 15px rgba(0, 0, 0, .2);
        transform: scale(.65);
    }
    75%
    {
        border-radius: 20%; 
        box-shadow: 0 45px 45px rgba(0, 0, 0, .15);
        transform: scale(.8);
    }
    100%
    {
        border-radius: 10%; 
        box-shadow: 0 25px 25px rgba(0, 0, 0, .1);
        transform: scale(1);
    }
}

@-webkit-keyframes scale1
{
    0%
    {
        border-radius: 10%; 
        box-shadow: 0 25px 25px rgba(0, 0, 0, .1);
        transform: scale(1);
    }
    25%
    {
        border-radius: 20%; 
        box-shadow: 0 5px 5px rgba(0, 0, 0, .15);
        transform: scale(.5);
    }
    50%
    {
        border-radius: 30%; 
        box-shadow: 0 15px 15px rgba(0, 0, 0, .2);
        transform: scale(.65);
    }
    75%
    {
        border-radius: 20%; 
        box-shadow: 0 45px 45px rgba(0, 0, 0, .15);
        transform: scale(.8);
    }
    100%
    {
        border-radius: 10%; 
        box-shadow: 0 25px 25px rgba(0, 0, 0, .1);
        transform: scale(1);
    }
}

#owl:before
{
    background: #dc9600;
    bottom: -65px;
    content: '';
    height: 180px;
    left: -80px;
    position: absolute;
            transform: rotate(-40deg);
       -moz-transform: rotate(-40deg);
        -ms-transform: rotate(-40deg);
